"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Camera, AlertCircle, CheckCircle } from "lucide-react"
import Link from "next/link"

// API URL from environment variable or default to localhost during development
const API_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5000"

export default function CameraDetection() {
  const [isStarted, setIsStarted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [message, setMessage] = useState<string | null>(null)

  const startCameraDetection = async () => {
    setIsLoading(true)
    setError(null)

    try {
      // Using the new endpoint from the updated Flask backend
      const response = await fetch(`${API_URL}/detect_camera`, {
        method: "GET",
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || "Failed to start camera detection")
      }

      const data = await response.json()
      setMessage(data.message)
      setIsStarted(true)
    } catch (err) {
      setError(err instanceof Error ? err.message : "An unknown error occurred")
      setIsStarted(false)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4 bg-gray-50">
      <Card className="w-full max-w-3xl">
        <CardHeader>
          <CardTitle className="text-2xl">Camera-Based Violence Detection</CardTitle>
          <CardDescription>Detect violent behavior in real-time using your camera</CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="bg-blue-50 border border-blue-200 rounded-md p-4">
            <h3 className="text-md font-medium text-blue-800 mb-2">How it works:</h3>
            <ol className="list-decimal pl-5 text-sm text-blue-700 space-y-1">
              <li>Click the "Start Camera Detection" button below</li>
              <li>A window will open on your computer showing the camera feed</li>
              <li>The system will analyze frames in real-time for violent behavior</li>
              <li>Results will be displayed directly on the camera feed</li>
              <li>Press 'q' on your keyboard to stop the detection and close the window</li>
            </ol>
          </div>

          <div className="bg-yellow-50 border border-yellow-200 rounded-md p-4">
            <h3 className="text-md font-medium text-yellow-800 mb-2">Important Notes:</h3>
            <ul className="list-disc pl-5 text-sm text-yellow-700 space-y-1">
              <li>This feature requires the Flask backend to run on your local machine</li>
              <li>The camera window will open on the machine running the Flask server</li>
              <li>Detection runs every 1.5 seconds to optimize performance</li>
              <li>Make sure your webcam is connected and working properly</li>
            </ul>
          </div>

          <div className="flex justify-center">
            <Camera className="h-24 w-24 text-gray-400" />
          </div>

          {/* Error Message */}
          {error && (
            <Alert variant="destructive">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Error</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Success Message */}
          {message && (
            <Alert className="border-green-500 bg-green-50">
              <CheckCircle className="h-4 w-4 text-green-600" />
              <AlertTitle className="text-green-700">Detection Started</AlertTitle>
              <AlertDescription className="text-green-600">
                {message}. A window should now be open on your computer showing the camera feed with violence detection.
              </AlertDescription>
            </Alert>
          )}
        </CardContent>
        <CardFooter className="flex flex-col space-y-4">
          <Button onClick={startCameraDetection} disabled={isLoading || isStarted} className="w-full">
            {isLoading ? "Starting..." : isStarted ? "Camera Detection Running" : "Start Camera Detection"}
          </Button>

          <Link href="/" className="w-full">
            <Button variant="outline" className="w-full">
              Back to Video Upload
            </Button>
          </Link>
        </CardFooter>
      </Card>
    </main>
  )
}

